























import ut from '../../utils/index.js';
import {mapState} from 'vuex'
export default {
	data() {
		return {
			list:[],
			src:''
		}
	},
	onLoad(opt) {
		this.src=opt.src;
	},
	computed: {
		...mapState(['adress'])
	},
	onShow() {
		this.req_getAdress();
	},
	methods: {
		setadress(item){
			if(this.src){
				this.$store.commit('setDeflfAdress', item)
				wx.navigateBack();
			}
		},
		go_mine_addadress(id){
			id=id||''
			wx.navigateTo({
				url: '../mine/addadress?_id='+id
			})
		},
		req_getAdress(){
			return ut.request({
				url: "address/list"
			}).then(data => {
				this.list=data;
				return data
			})
		},
		req_setDef(item){
			ut.request({
				data:{
				  id: item.id
				},
				url: "address/setAttr"
			}).then(data => {
				this.req_getAdress();
			})
		},
		req_del(item,index){
			ut.request({
				data:{
				id: item.id
				},
				url: "address/delete"
			}).then(data => {					
				this.req_getAdress('del').then((data)=>{
					if(this.src && item.id==this.adress.id){
						this.$store.commit('setDeflfAdress', data?data[0]:data)
					}
				});
				
			})
		}
	}
	
}
